import { build } from 'esbuild'
import path from 'path'
import { green } from 'picocolors'

import { scanPlugins } from './scanPlugin'
import { PRE_BUNDLE_DIR } from '../../constant'
import { preBundlePlugin } from './preBundlePlugin'

export const optimizer = async (root) => {
  // 1. Á°ÆÂÆöÂÖ•Âè£
  const entry = path.resolve(root, 'src/main.tsx')
  
  // 2. ‰ªéÂÖ•Âè£Â§ÑÊâ´Êèè‰æùËµñ
  const deps = new Set<string>()
  await build({
    entryPoints: [entry],
    bundle: true,
    write: false,
    plugins: [await scanPlugins(deps)]
  })
  console.log(
    `${green('ÈúÄË¶ÅÈ¢ÑÊûÑÂª∫ÁöÑ‰æùËµñ')}:\n${[...deps]
      .map(green)
      .map((item) => `  ${item}`)
      .join('\n')}`
  )
  // 3. È¢ÑÊûÑÂª∫‰æùËµñ
  await build({
    entryPoints: [...deps],
    bundle: true,
    write: true,
    splitting: true,
    format: 'esm',
    outdir: path.resolve(root, PRE_BUNDLE_DIR),
    plugins: [await preBundlePlugin(deps)]
  })
  console.log('ÊûÑÂª∫ÊàêÂäüÔºÅ');
  
}
